
.top {
  width: 1000px;
  margin: 0 auto;
  position: relative;
}
.top > .head {
  position: absolute;
  top: 0;
  right: 0;
}
.top > .head > i {
  display: inline-block;
  width: 50px;
  height: 50px;
  background: url("../assets/touxiang.png");
  background-size: cover;
}
.top > .head > span {
}
.title {
  font-size: 32px;
  font-weight: 700;
  line-height: 130px;
}
.desc {
  color: #313131;
  font-size: 20px;
  line-height: 30px;
  clear: both;
  margin-top: 72px;
}
.time {
  font-size: 15px;
  color: #969899;
  line-height: 30px;
  float: right;
}
.category {
  float: left;
}
.category > ul {
  list-style: none;
  overflow: hidden;
}
.category > ul > li {
  width: auto;
  padding: 0 20px;
  text-align: center;
  height: 30px;
  line-height: 30px;
  border-radius: 25px;
  float: left;
  border: 1px solid #dcdee0;;
  margin-right: 25px;
  color: #969899;
}
.work {
  width: 100%;
  background: #f8f9fa;
  overflow: hidden;
  margin-top: 120px;
  padding-top: 50px;
}
.worklist {
  width: 1000px;
  margin: 0 auto;
}
.worklist > ul {
  list-style: none;
  -webkit-column-count: 2;
          column-count: 2;
}
.worklist ul li {
  float: left;
  width: 450px;
  margin-bottom: 50px;
  margin-right: 25px;
  cursor: pointer;
  -webkit-box-shadow: 1px 1px 5px 0 #8e8a8a;
          box-shadow: 1px 1px 5px 0 #8e8a8a;
}
.mymodal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 20;
}
.mymodal > .conte {
  margin: 30px auto;
  overflow: hidden;
  border-radius: 5px;
  padding: 15px;
}
.iconfontyyy {
  font-family: "iconfont", serif !important;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  font-size: 24px;
  background: transparent;
  color: #FFF;
}



